import { PassGenerator } from './modules/generatePass';
import './assets/css/style.css';

const onLoad = () => {

	const sizeInput = document.querySelector('#size');
	const numbers = document.querySelector('#numbers');
	const ucLetters = document.querySelector('#uc-letters');
	const lcLetters = document.querySelector('#lc-letters');
	const symbols = document.querySelector('#symbols');
	const btnGenerate = document.querySelector('#generate');
	const result = document.querySelector('#result');

	btnGenerate.addEventListener('click', e => {
		e.preventDefault();
		
		//Opções: tamanho; incluir números; maiúsculas; minúsculas; símbolos; onde exibir
		const options = {
			size: sizeInput.value,
			numbers: numbers.checked,
			upperCase: ucLetters.checked,
			lowerCase: lcLetters.checked,
			symbols: symbols.checked,
			result: result
		};
		
		let password = new PassGenerator(options);
		password.generate();
	})
}//end onLoad()

onLoad();
