export class PassGenerator {

	constructor(options) {
		this.numbers = options.numbers;
		this.upperCase = options.upperCase;
		this.lowerCase = options.lowerCase;
		this.symbols = options.symbols;
		this.result = options.result;
		this.size = options.size;
	}

	_generatePass() {
		let allChars = '';
		let password = '';

		if (this.upperCase) allChars += 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
		if (this.lowerCase) allChars += 'abcdefghijklmnopqrstuvwxyz';
		if (this.numbers) allChars += '0123456789';
		if (this.symbols) allChars += '!@#$%&*()-+=[]{}_¢£§=/?|';

		for (let i = 0; i < this.size; i++) {
			let char = allChars.charAt(Math.floor(Math.random() * allChars.length));
			password += char;
		}
		return password;
	}

	_checkSize() {
		if (this.size < 8 || this.size > 32) {
			this._showWarning('Escolha um número entre 8 e 32 para o tamanho da senha');
			return false;
		} else {
			return true;
		}
	}

	_checkOptions() {
		if (this.numbers || this.upperCase || this.lowerCase || this.symbols) { return true }
		else this._showWarning('Escolha ao menos uma opção:');
	}

	generate() {
		this.result.className = '';
		this.result.innerText = '';
		if (this._checkSize() && this._checkOptions()) {
			let password = this._generatePass();
			this.result.innerText = password;
		}
	}

	_showWarning(message) {
		result.innerText = message;
		result.className = 'warning';
	}

}
